import React from "react";

export default function ImgContainer(props) {
  const { margin, images, justify } = props;

  return (
    <div
      className="imgSort"
      style={{
        margin: `${margin}px auto`,
        justifyContent: justify,
      }}
    >
      {images.map((obj, i) => (
        <a href={obj.link} key={i} target="_blank" rel="noreferrer">
          <img src={obj.img} width={obj.width} alt={`${obj.img}`} />
        </a>
      ))}
    </div>
  );
}
